


































































import { defineComponent, ref } from '@nuxtjs/composition-api'
import ConfirmDelete from '~/components/util/ConfirmDelete.vue'

export default defineComponent({
  components: {
    ConfirmDelete
  },
  props: {
    handleBack: {
      type: Function,
      default: null
    },
    handleEdit: {
      type: Function,
      default: null
    },
    handleSubmit: {
      type: Function,
      default: null
    },
    handleReset: {
      type: Function,
      default: null
    },
    handleDelete: {
      type: Function,
      default: null
    },
    handleAdd: {
      type: Function,
      default: null
    },
    title: {
      type: String,
      default: ''
    },
    isLoading: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const confirmDelete = ref(false)

    const goBack = () => {
      if (props.handleBack) {
        // @ts-ignore
        props.handleBack()
      }
    }

    const addItem = () => {
      if (props.handleAdd) {
        // @ts-ignore
        props.handleAdd()
      }
    }

    const editItem = () => {
      if (props.handleEdit) {
        // @ts-ignore
        props.handleEdit()
      }
    }

    const submitItem = () => {
      if (props.handleSubmit) {
        // @ts-ignore
        props.handleSubmit()
      }
    }

    const resetItem = () => {
      if (props.handleReset) {
        // @ts-ignore
        props.handleReset()
      }
    }

    return {
      confirmDelete,
      goBack,
      addItem,
      editItem,
      submitItem,
      resetItem
    }
  }
})
