




























import { computed, defineComponent } from '@nuxtjs/composition-api'

export default defineComponent({
  props: {
    handleDelete: {
      type: Function,
      default: () => {}
    },
    visible: {
      type: Boolean,
      default: true
    }
  },
  setup (props, { emit }) {
    const setVisible = (show: boolean) => {
      emit('close')
    }

    const show = computed(() => props.visible)

    return {
      setVisible,
      show
    }
  }
})
